.App-contact {
    margin-bottom: 7em;
    color: #fff;
}

.contacts {
    display: flex;
    justify-content: space-evenly;
}

.App-contact h1 {
    margin-bottom: 1em;
}

.contact-form {
    border: 1px solid;
    border-radius: 8px;
    padding: 20px 30px;
}

.contact-form input{
    padding: 10px 10em;
    margin: 10px 0;
    border-radius: 5px;
    border: 1px solid;
}

.contact-form textarea {
    padding: 2em 10em;
    margin: 10px 0;
    resize: none;
    border-radius: 5px;
    border: 1px solid;
}